<template>
    <div class="week">
        <div class="week-title">
            周末去哪
        </div>
        <div class="content" v-for="(item,index) in list" :key="index">
            <img :src="item.imgUrl" alt="">
            <div class="title">{{item.title}}</div>
            <div class="desc">{{item.desc}}</div>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeWeek",
    props:{
        list:Array
    }
}
</script>
<style scoped>

    .week-title{
        line-height: 35px;
        background: skyblue;
        padding-left: 10px;
    }
    .title{
        margin-top: 10px 5px;
        font-size: 16px;
        padding: 10px 10px 0 10px;
    }
    .desc{
        color: gray;
        padding: 10px;
         font-size: 14px;
    }
</style>


