<template>
    <div class="letter">
        <ul class="list">
            <li class="item" v-for="(item,index) in citys" 
            :key="index"
            @click="handleLetter" 
           
            >
                {{index}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityLetter",
    data(){
        return {
             moveStatus:false
        }
    },
    props:{
        citys:Object
    },
    methods:{
        handleLetter(e){
            //console.log(e.target.innerText)
            let letter = e.target.innerText;
            this.$emit("letterHandle",letter)
        }

    }
}
</script>
<style scoped>

    .item{
        line-height: 30px;
    }
    .list{
        position: absolute;
        right: 10px;
        top: 15%;
    }
</style>


