<template>
    <div class="recommemd">
        <div class="title">
            热销推荐
        </div>
        <div class="content" v-for="(item,index) in list" :key="index">
            <div class="left">
                <img :src="item.imgUrl" alt="">
            </div>
            <div class="ri">
                <div class="top">{{item.title}}</div>
                <div class="center">{{item.desc}}</div>
                <a href="" id="buttom">查看详情</a>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeRecommemd",
    props:{
        list:Array
    }
}
</script>
<style scoped>
    .title{
       line-height: 35px;
        background: gray;
    }
    .content{
        display: flex;
        padding: 10px;
        
    }
   .left img{
       width: 100px;
       height: 100px;
   }
   .ri{
       display: flex;
       flex-direction: column;
       justify-content: space-around;
       padding-left: 15px;
   }
   .top{
       font-size: 16px;
   }
   .center{
       color: gray;
   }
   #buttom{
       color: white;
       background-color: orange;
       width: 100px;
       text-align: center;
       line-height: 30px;
      border-radius: 10px;
   }
</style>


