<template>
    <div class="ico-list">
        <mt-swipe :auto="0">

            <mt-swipe-item v-for="(item,index) in pages" :key="index">
                <div class="ico" v-for="(ite,i) in item" :key="i">
                    <div class="im">
                        <img :src="ite.imgUrl" alt="">
                    </div>
                    <div class="desc">
                        {{ite.desc}}
                    </div>
                </div>
            </mt-swipe-item>
            
        </mt-swipe>
    </div>
</template>
<script>
export default {
    name:"HomeIconList",
    props:{
        list:Array
    },
    computed:{
        pages(){
            let pages = [];
            this.list.forEach((item,index)=>{
                var page = Math.floor(index/8);//拿到下标0-7
                if(!pages[page]){
                    pages[page]=[]
                }
                pages[page].push(item)
            })
            return pages
        }
    }
}
</script>
<style scoped>
    .ico-list >>> mint-swipe-indicator is-active{
        background: red;
    }
     .ico-list >>> mint-swipe-item{
        background:green;
    }
    .ico-list{
        height: 250px;
    }
    .ico{
        float: left;
        box-sizing: border-box;
        width: 25%;
        text-align: center;
        padding: .266667rem;
    }
    .im img{
        width: 100%;
    }
    .desc{
        margin-top: .213333rem
    }
</style>


