<template>
    <div class="city">
        <city-header></city-header>
        <city-search :citys="citys"></city-search>
        <city-list :citys="citys" :hotCitys="hotCitys" :letter="letter"></city-list>
        <city-letter :citys="citys" @letterHandle="receive"></city-letter>
    </div>
</template>
<script>

import CityHeader from "./components/Header"
import CitySearch from "./components/Search"
import CityList from "./components/List"
import CityLetter from "./components/Letter"

import axios from 'axios'

export default {
    name:"City",
    components:{
        CityHeader,
        CitySearch,
        CityList,
        CityLetter
    },
    data(){
        return {
            citys:{},       //cities: {A: Array(18), B: Array(28), C: Array(46), D: Array(50), E: Array(4), 
            hotCitys:[],    // [{…}, {…}, {…}, {…}, {…}, {…}]
            letter:""
        }
    },
    mounted(){
        this.getCityDate()
    },
    methods:{
        getCityDate(){
               axios.get("/tour/static/api/city.json")
               .then((res)=>{
                   console.log(res.data.data)
                   var data = res.data.data;
                   this.citys=data.cities;
                   this.hotCitys=data.hotCities;
               })
         },
         receive(letter){
             this.letter = letter
         } 
    }
}
</script>
<style scoped>

</style>


