<template>
    <div class="home">
        
       <home-header></home-header>
        <home-swipper :list="swippeList"></home-swipper>
        <home-icon-list :list="iconList"></home-icon-list>
        <home-recommemd :list="recommemdList"></home-recommemd>
        <home-week :list="weekList"></home-week>
    </div>
</template>

<script>
import HomeHeader from './components/Header'
import HomeSwipper from './components/Swipper'
import HomeIconList from './components/IconList'
import HomeRecommemd from './components/Recommemd'
import HomeWeek from './components/Weekend'

import axios from 'axios'

export default {
    name:"Home",
    components:{
        HomeHeader,
        HomeSwipper,
        HomeIconList,
        HomeRecommemd,
        HomeWeek
    },
    data(){
        return {
            swippeList:[],
            iconList:[],
            recommemdList:[],
            weekList:[]
        }
        
    },
    mounted(){
        this.getHomeDate()
    },
    methods:{
           getHomeDate(){
               axios.get("/tour/static/api/index.json")
               .then((res)=>{
                   console.log(res.data.data)
                   var data = res.data.data;
                   this.swippeList = data.swiperList
                   this.iconList = data.iconList
                   this.recommemdList = data.recommendList
                   this.weekList = data.weekendList
               })
           } 
        }
}
</script>

<style scoped>

</style>


