<template>
    <div>
        <div class="search">
            <input v-model="keyWord" type="text" placeholder="请输入城市或拼音">
        </div>

        <div class="search-content" v-show="keyWord">
            <ul>
                <li class="item" v-for="(item,index) in list" :key="index">{{item.name}}</li>
                
            </ul>
        </div>
     </div>
</template>
<script>
export default {
    name:"CitySearch",
    data(){
        return {
            keyWord:"",
            list:[],
            timer:null
        }
    },
    props:{
        citys:Object
    },
    //侦听如果keyWord变化执行
    watch:{
        
        keyWord(){
            var arr = [];

            if(this.timer){
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(()=>{
                 //console.log(this.citys)
                for( let index in this.citys){
                    //console.log(index)
                    this.citys[index].forEach(element => {
                        if(element.name.indexOf(this.keyWord) >-1 || element.spell.indexOf(this.keyWord) >-1){
                            arr.push(element)
                        }
                    });
                }
                this.list = arr
            },1000)
           
        }
    }
}
</script>
<style scoped>
    .search{
        background:#00bcd4;
        line-height: 40px;
        color: gray;
        margin-top: 1px;
        text-align: center;
    }
    input{
        width: 90%;  
        line-height: 28px;
        text-align: center;
        border-radius: 8px;
    }
    .search-content{
        position: absolute;
        background: #eee;
        left: 0;
        right: 0;
        top: 80px;
        bottom: 0;

    }
    .item{
        line-height: 35px;
        padding-left: 10px;
        background: #fff;
    }
</style>


