<template>
   <div class="city-list" ref="wrapper">
       <div>
             <!-- 当前城市 -->
            <div class="current">
                <h2 class="title">当前城市</h2>
                <div class="current-list">
                    <div class="button-wrapper">
                        <div class="button">北京</div>
                    </div>
                    <div class="button-wrapper">
                        <div class="button">上海</div>
                    </div>
                    <div class="button-wrapper">
                        <div class="button">广州</div>
                    </div>
                    <div class="button-wrapper">
                        <div class="button">深圳</div>
                    </div>
                    <div class="button-wrapper">
                        <div class="button">杭州</div>
                    </div>
                </div>
            </div>

            <!-- 热门城市 -->
            <div class="hot">
                <h2 class="title">热门城市</h2>
                <div class="current-list">

                    <div class="button-wrapper" v-for="(item,index) in hotCitys" :key="index">
                        <div class="button">{{item.name}}</div>
                    </div>

                </div>
            </div>   

            <!-- 城市列表 -->
            <div class="list" v-for="(item,index) in citys" :key="index" :ref="index">
                <h2 class="title">{{index}}</h2>
                <ul>
                    <li class="item" v-for="(ite,i) in item" :key="i">{{ite.name}}</li>
                </ul>
            </div>

       </div>
   </div>
   
</template>
<script>

import BScroll from 'better-scroll'

export default {
    name:"CityList",
    props:{
        citys:Object,
        hotCitys:Array,
        letter:String
    },
    mounted(){
        this.$nextTick(() => {
            let wrapper = this.$refs.wrapper;
            this.scroll = new BScroll(wrapper)
         })
    },
    //监听器监听letter数据项，发生变化执行此函数
    watch:{
        letter(){
            console.log(this.$refs[this.letter][0])
            this.scroll.scrollToElement(this.$refs[this.letter][0])
        }
    }
}
</script>
<style scoped>
    .title{
        line-height: 30px;
        background: #eee;
    }
    .current-list{
        display: flex;/*默认在一行显示*/
        flex-wrap: wrap;
    }
    .button-wrapper{
        width: 33.33%;
    }
    .button{
        border: 1px solid gray;
        box-sizing: border-box;
        text-align: center;
        padding:5px 0px;
        margin: 5px 15px;
        border-radius: 8px;
    }
    .item{
        line-height: 35px;
        margin-left: 10px;
    }
    .city-list{
        position: absolute;
        left: 0;
        right: 0;
        top: 83px;
        bottom: 0;
        z-index: -999;

    }
    
</style>


